.sec-banner {
    padding: 0;
}

.carousel {

    &,
    & .carousel-item {
        height: 500px;
    }

    .full-banner &,
    .full-banner & .carousel-item {
        height: 100vh;
    }

    & .carousel-item {
        background-size: cover;
        background-position: center;

        & iframe {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            bottom: 0;
            border: 0;
        }

        & .carousel-caption {
            top: 50%;
            bottom: auto;
            left: 50%;
            right: auto;
            width: 80%;
            transform: translate(-50%, -50%);

            & .carousel-caption__title {
                font-weight: 700;
                font-size: 50px;
            }

            & .carousel-caption__text {
                font-size: 25px;
            }

            & .carousel-caption__btn {
                border-radius: 5px;
                padding: 12px 30px;

                &:not(:last-child) {
                    margin: 0 15px 0 0;
                }

            }
        }
    }

    .multi-carousel & {
        height: 150px;
        overflow: hidden;

        & .carousel-inner {
            width: 90%;
            margin: 0px auto;

            & .carousel-item {
                height: 150px;

                & .client-logo {
                    float: left;
                    line-height: 150px;

                    & img {
                        width: 100%;
                    }
                }
            }
        }
    }

    &.parallax-slide {
        & .carousel-item {
            background-repeat: no-repeat;
            background-attachment: fixed;
        }
    }
}

.multi-carousel {

    & .carousel-control-prev,
    & .carousel-control-next {
        width: 4%;
        margin-left: 0px;
        background-image: none;
    }

    & .carousel-control-prev-icon {
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%22aaa' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E");
    }

    & .carousel-control-next-icon {
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%22aaa' viewBox='0 0 8 8'%3E%3Cpath d='M1.5 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E")
    }
}

/* Tablet, Wide and Small Phone CSS */
@media only screen and (max-width:999px) {
    .carousel {

        &,
        & .carousel-item {
            height: 300px;

            & .carousel-caption {

                //height: 50%;
                & .carousel-caption__title {
                    font-weight: 600;
                    font-size: 40px;
                    line-height: 45px;
                }

                & .carousel-caption__text {
                    font-size: 25px;
                }

                & .carousel-caption__btn {
                    padding: 7px 30px;
                }
            }
        }

        .full-banner .sec-banner &,
        .full-banner .sec-banner & .carousel-item {
            height: 50vh;
        }
    }

    .multi-carousel .carousel-inner .carousel-item {
        height: 140px; //display: flex;
    }
}

@media only screen and (max-width:999px) and (orientation: landscape) {

    .full-banner .sec-banner .carousel,
    .full-banner .sec-banner .carousel .carousel-item {
        height: 100vh;
    }
}

/* Tablet CSS */
@media only screen and (max-width:999px) and (min-width:768px) {
    .multi-carousel .carousel .active>div {
        //display: none;
    }

    .multi-carousel .carousel .active>div:first-child,
    .multi-carousel .carousel .active>div:first-child+div,
    .multi-carousel .carousel .active>div:first-child+div+div,
    .multi-carousel .carousel .active>div:first-child+div+div+div {
        display: block;
    }
}

/* Wide and Small Phone CSS */
@media only screen and (max-width:767px) {
    .carousel {
        & .carousel-item {
            .carousel-caption {

                //height: 50%;
                & .carousel-caption__title {
                    font-weight: 600;
                    font-size: 22px;
                    line-height: 30px;
                }

                & .carousel-caption__text {
                    font-size: 13px;
                }

                & .carousel-caption__btn {
                    padding: 7px 20px;
                }
            }
        }
    }

    .multi-carousel .carousel .active>div {
        //display: none;
    }

    .multi-carousel .carousel .active>div:first-child,
    .multi-carousel .carousel .active>div:first-child+div {
        display: block;
    }

    .sec-footer .sec-footer__intouch {
        width: 90%;
        margin: 0px auto;
    }
}